/* montserrat-100 - latin */
@font-face {
	font-family: 'Montserrat';
	font-style: normal;
	font-weight: 100;
	src: url('../webfonts/montserrat-v18-latin-100.eot'); /* IE9 Compat Modes */
	src: local(''),
		 url('../webfonts/montserrat-v18-latin-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
		 url('../webfonts/montserrat-v18-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
		 url('../webfonts/montserrat-v18-latin-100.woff') format('woff'), /* Modern Browsers */
		 url('../webfonts/montserrat-v18-latin-100.ttf') format('truetype'), /* Safari, Android, iOS */
		 url('../webfonts/montserrat-v18-latin-100.svg#Montserrat') format('svg'); /* Legacy iOS */
  }
